/* lang selector */
.vjs-lang-button {
  float: right;
  line-height: 3em;
  text-align: justify !important; }

/* Don't show hover effects on title */
ul li.vjs-menu-title.vjs-lang-menu-title:hover {
  cursor: default;
  background-color: transparent;
  color: white;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  box-shadow: none; }

/* Needed to keep text visible in video.js 4.9 */
.vjs-lang-button .vjs-control-text {
  width: auto;
  height: auto;
  clip: auto;
  display: none; 
}

.vjs-menu .vjs-menu-content {
  font-family: sans-serif;
  font-weight: bold; }

