.vjs-menu {
  .vjs-menu-content {
    font-family: sans-serif;
    font-weight: bold;
  }
}

.video-js-title {
  position: absolute;
  font-family: 'Bebas-Bold', sans-serif;
  top: 20px;
  left: 20px;
  z-index: 9999999999; /* Nunca puse un z index tan alto, que barbaridad!!! */
  color: $white;
  display: none;

  .btn-back {
    color: $white;
    padding: 0 20px;
    font-size: 1.1em;

    &:hover {
      color: #ff6d00;
    }
  }
}

.btn-back, h1 {
  display: inline-block;
  vertical-align: middle;
}

h1 {
  margin: 0;
}

.hide {
  display: none !important;
}

.show {
  display: block !important;
}

body {
  margin: 0;
  padding: 0;
  background: #000;
}

.vid1-dimensions {
  width: 100% !important;
  height: 100% !important;
