/* Colors
 * ---------------------
 */

/* Basic Naming Colors
 * ---------------------
 * To find an appropriate name, please visit: http://chir.ag/projects/name-that-color/
 */

	$white:							rgb(255,255,255);			// #FFFFFF
	$black:							rgb(0,0,0);					// #000000

	$scooter: 						rgb(38,198,218); 			// #26C6DA cyan400 logo
	$blaze-orange:					rgb(255,109,0);				// #FF6D00 Orange A700 link & logo
	$trinidad: 						rgb(230,81,0);				// #E65100 Orange 900 link press
	$web-orange:					rgb(255,171,0);				// #FFAB00 Amber A700 link hover

	$guardsman-red:					rgb(213,0,0);				// #D50000 Red A700 error
	$green-haze:					rgb(0,168,68);				// #00A844 success
	$gold:							rgb(255,212,0);				// #FFD400 warning

	$prussian-blue:					rgb(0,33,71);				// #002147 background
	$midnight:						rgb(0,20,40);				// #001428 dark background
	$blue-charcoal:					rgb(0,10,17);				// #000a11 Very dark background

	$big-stone:						rgb(21,44,68);				// #152C44 disabled background
	$chello:						rgb(28,63,89);				// #1C3F59 disabled text

	$dark-prussian:					rgb(0,45,89);				// #002D59 popover background

	$bali-hai:						rgb(140,155,172);			// #8C9BAC input placeholders

	$lochmara:						rgb(2,136,209);				// #0288D1 Light Blue 700 rent tag
	$bahama-blue:					rgb(1,87,155);				// #01579B Light Blue 900 shadow rent tag

	$shark:							rgb(33,40,44);				// #21282c Borde de la ficha

	/* Social network colors */
	$chambray:						rgb(59,89,152);				// #3b5998 facebook
	$picton-blue:					rgb(85,172,238);			// #55acee twitter
	$thunderbird:					rgb(205,32,31);				// #cd201f youtube
	$monza:							rgb(189,8,28);				// #bd081c pinterest
	$cerize:						rgb(214,47,128);			// #d62f80 instagram

	/* Main colors */
	$brand-default:					$blaze-orange !default;
	$brand-primary: 				$brand-default !default;
	$brand-success: 				$green-haze !default;
	$brand-info:					$lochmara !default;
	$brand-warning:					$gold !default;
	$brand-danger:					$guardsman-red !default;
	$brand-dark:					$blue-charcoal !default;
	$brand-dislabed:				$big-stone !default;

	/* For opacity/transparency purposes */
	$dark:							$brand-dark;
	$light: 						$white;

	/* Colors for carousel array. You can add or remove as many colors as you want,
	then you have to add or remove them from the array. */
	$flamingo: 						rgb(239,80,69); 			// #EF5045
	$glacier:						rgb(113,187,197);			// #71BBC5
	$jungle-green:					rgb(43,173,145);			// #2BAD91
	$lightning-yellow:				rgb(255,190,23);			// #FFBE17

