/* Typography
 * ---------------------
 */

	/* Basic Qubit Fonts */
	$roboto-regular: 'Roboto-Regular', sans-serif;
	$roboto-medium: 'Roboto-Medium', sans-serif;
	$roboto-condensed: 'Roboto-Condensed', sans-serif;
	$roboto-condensed-light: 'Roboto-Condensed-Light', sans-serif;

	/* Defining fonts */
	$font-family-sans-serif:  	$roboto-regular !default;
	$font-family-base:        	$font-family-sans-serif;
	$font-family-base-bold:   	$roboto-medium;

	$font-size-base:          	16px;

	$font-size-desktop:			$font-size-base;
	$font-size-tablet:			$font-size-desktop - 2;

	//** By default, this inherits from the `<body>`.*/
	$h1-headings-font-family:   	$roboto-condensed !default;
	$h2-headings-font-family:    	$h1-headings-font-family !default;
	$h3-headings-font-family:		$h1-headings-font-family !default;
	$h4-headings-font-family:		$h1-headings-font-family !default;
	$h5-headings-font-family:		$h1-headings-font-family !default;
	$h6-headings-font-family:		$h1-headings-font-family !default;

	//Default Headings font size
	// Mobile
	$font-size-h1-mobile:					22px !default;
	$font-size-h2-mobile:					20px !default;
	$font-size-h3-mobile:					18px !default;
	$font-size-h4-mobile:					16px !default;
	$font-size-h5-mobile:					14px !default;
	$font-size-h6-mobile:					12px !default;

	// Tablet
	$font-size-h1-tablet:					24px !default;
	$font-size-h2-tablet:					20px !default;
	$font-size-h3-tablet:					18px !default;
	$font-size-h4-tablet:					16px !default;
	$font-size-h5-tablet:					14px !default;

	// Desktop
	$font-size-h1-desktop:					30px !default;
	$font-size-h2-desktop:					24px !default;

	// Default headings font weight
	$headings-font-weight:    		normal !default;

	$h1-headings-font-weight:    	$headings-font-weight;
	$h2-headings-font-weight:    	300 !default;
	$h3-headings-font-weight:    	300 !default;
	$h4-headings-font-weight:    	$headings-font-weight;
	$h5-headings-font-weight:    	$headings-font-weight;
	$h6-headings-font-weight:    	$headings-font-weight;

	// Default Headings line height
	$headings-default-line-height:	1.3 !default;

	// Default headings color
	$headings-color:          		inherit !default;
	$h1-text-shadow: $black;
	$item-title-text-shadow: $black;

//== Grid system
//## Define our custom responsive grid.

	//** Number of columns in the grid.
	$grid-columns:              12 !default;
	//** Padding between columns. Gets divided in half for the left and right.
	$grid-gutter-width-em:         30px !default;

	//== Container sizes
	//## Define the maximum width of `.container` for different screen sizes.

	// Small screen / tablet
	$container-tablet:             (720px + $grid-gutter-width-em); //* 720px *//
	$container-sm:                 $container-tablet;

	// Medium screen / desktop
	$container-desktop:            (940px + $grid-gutter-width-em); //* 940px *//
	$container-md:                 $container-desktop;

	// Large screen / wide desktop
	$container-large-desktop:      (1140px + $grid-gutter-width-em); //* 1140px *//
	$container-lg:                 $container-large-desktop;

	// Extra Large screen / Big desktop
	$container-big-desktop:(1360px + $grid-gutter-width-em); //* 1360px *//
	$container-xlg:$container-big-desktop;

	// Large screen / wide desktop
	$container-enormous-desktop:( 1860px + $grid-gutter-width-em) ;//* 1860px *//
	$container-mxlg:$container-enormous-desktop;

// Define the breakpoints at which your layout will change, adapting to different screen sizes.
// NOTE: currently we are only using some of this breakpoints majorly as layout changes. Please refer to layout.scss

	// Extra small screen / phone
	$screen-xxs:				480px;
	$screen-xxs-min:			$screen-xxs;
	$screen-phone-vertical:		$screen-xxs-min;

	// Small screen / phone
	$screen-xs:                 640px;
	$screen-xs-min:            	$screen-xs;
	$screen-phone-horizontal:	$screen-xs-min;

	// Small screen / tablet
	$screen-sm:                 768px;
	$screen-sm-min:             $screen-sm;
	$screen-tablet:             $screen-sm-min;

	// Medium screen / desktop
	$screen-md:					992px;
	$screen-md-min:				$screen-md;
	$screen-desktop:			$screen-md-min;

	// Large screen / wide desktop
	$screen-lg:                  1200px;
	$screen-lg-min:              $screen-lg;
	$screen-lg-desktop:          $screen-lg-min;

	// Extra-Large screen / wide desktop
	$screen-xlg:                  1440px;
	$screen-xlg-min:              $screen-xlg !default;
	$screen-xlg-desktop:		  $screen-xlg-min !default;

	// Mega-Large screen / wide desktop
	$screen-mxlg:                  1920px !default;
	$screen-mxlg-min:              $screen-mxlg !default;
	$screen-tv:					   $screen-mxlg-min !default;

	// So media queries don't overlap when required, provide a maximum
	$screen-xs-max:              ($screen-sm-min - 1);
	$screen-sm-max:              ($screen-md-min - 1);
	$screen-md-max:              ($screen-lg-min - 1);
	$screen-lg-max:              ($screen-xlg-min - 1);
	$screen-xlg-max:             ($screen-mxlg-min - 1);

	// Navbar collapse
	//** Point at which the navbar becomes uncollapsed.
	$grid-float-breakpoint:     $screen-sm-min !default;
	//** Point at which the navbar begins collapsing.
	$grid-float-breakpoint-max: ($grid-float-breakpoint - 1) !default;

